<template>
    <div class="LoginLog">
        <MyMenu />
        <a-row>
            <a-col :span="this.g.container.span" :offset="this.g.container.offset">

                <a-table :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id">
                    <span slot="action" slot-scope="text, record">
                        <div class="tabBtn">
                            <a-popover placement="bottomRight" overlayClassName="tableBtn">
                                <template slot="title">
                                    <a href="javascript:;" @click="handleAdd(record)">
                                        <i class="iconfont icon-table-edit" />编辑
                                    </a>
                                    <a href="javascript:;" @click="deleHostData(record)">
                                        <i class="iconfont icon-tableEmpty" />删除
                                    </a>
                                </template>
                                <span>
                                    <i class="iconfont icon-tableMore" />
                                </span>
                            </a-popover>
                        </div>
                    </span>
                </a-table>

            </a-col>
        </a-row>
    </div>
</template>


<script>
function validBrowser(u_agent) {
    // var u_agent = navigator.userAgent;
    u_agent = u_agent.toLocaleLowerCase();
    var browser_name = "Failed to identify the browser";
    // browser_name = u_agent;
    if (u_agent.indexOf("mobile") != -1) {
        if (u_agent.indexOf("iphone") != -1) {
            browser_name = "iPhone";
        } else if (u_agent.indexOf("pixel 4") != -1) {
            browser_name = "Pixel 4";
        } else if (u_agent.indexOf("Android ") != -1) {
            browser_name = "Android ";
        }
        return browser_name;
    }
    if (u_agent.indexOf("firefox") > -1) {
        browser_name = "Firefox";
    } else if (u_agent.indexOf("chrome") > -1) {
        browser_name = "Chrome";
    } else if (
        u_agent.indexOf("trident") > -1 &&
        u_agent.indexOf("rv:11") > -1
    ) {
        browser_name = "IE11";
    } else if (
        u_agent.indexOf("msie") > -1 &&
        u_agent.indexOf("trident") > -1
    ) {
        browser_name = "IE(8-10)";
    } else if (u_agent.indexOf("msie") > -1) {
        browser_name = "IE(6-7)";
    } else if (u_agent.indexOf("opera") > -1) {
        browser_name = "Opera";
    } else {
        browser_name += ",info:" + u_agent;
    }
    // document.write("browser_name:" + browser_name + "<br>");
    // document.write("u_agent:" + u_agent + "<br>");
    return browser_name;
}

import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "LoginLog",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,
            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 5,
                // showSizeChanger: true,
                // pageSizeOptions: ["10", "20", "50", "100"], //每页中显示的数据
                // showTotal: (total) => `共有 ${total} 条数据`, //分页中显示总的数据
            },
            queryParam: {
                page: 1,
                size: 5,
                user_id: this.g.userInfo.id,
            },
            columns: [
                {
                    title: "IP",
                    dataIndex: "ip",
                    width: "20%",
                },
                {
                    title: "Device",
                    dataIndex: "browser",
                },
                {
                    title: "Login Time",
                    dataIndex: "create_datetime",
                    width: "20%",
                },
            ],
        };
    },
    methods: {
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            this.getTableList();
        },
        formatDate(list) {
            // let _t=this
            let a = list;
            a.forEach(function (item) {
                item.create_datetime = moment(item.create_datetime).format(
                    "YYYY-MM-DD HH:mm:ss"
                );
                item.browser = validBrowser(item.user_agent);
            });
            return a;
        },
        getTableList() {
            this.loading = true;
            $.get(
                `${this.g.apiUrl}/api/my/login-log`,
                this.queryParam,
                (res) => {
                    this.loading = false;

                    if (res.flag != "ok") {
                        //提示错误
                        this.$message.error(res.flag);
                        return;
                    }

                    const pagination = { ...this.pagination };
                    pagination.total = res.data.page.total;
                    this.tableDatas = this.formatDate(res.data.list);
                    this.pagination = pagination;
                }
            );
        },
        init() {
            this.getTableList();
        },
    },
    mounted() {
        this.init();
    },
};
</script>



